/* infographic */

.infoGraphicContainer {
	height: 210px;
	border-radius: 15px;
	margin-top: 15px;
	padding:15px 0px 10px 0px;
	text-align: center;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 50px 50px;
	grid-template-areas: "locationContainer" "informationContainer";

  }
  
  .locationContainer {
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1fr 1fr;
	grid-template-areas: "shortLocationContainer" "longLocationContainer";
	grid-area: locationContainer;
  }
  
  .shortLocationContainer {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	grid-template-areas: "shortAmazonLocation truckIcon shortUserLocation";
	grid-area: shortLocationContainer;
  }
  
  .truckIcon { grid-area: truckIcon; }
  
  .shortUserLocation { grid-area: shortUserLocation; }
  
  .shortAmazonLocation { grid-area: shortAmazonLocation; }
  
  .longLocationContainer {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	grid-template-areas: "fullAmazonLocation emptyContainer fullUserLocation";
	grid-area: longLocationContainer;
  }
  
  .fullAmazonLocation { grid-area: fullAmazonLocation; }

  .emptyContainer { grid-area: emptyContainer; }
  
  .fullUserLocation { grid-area: fullUserLocation; }
  
  .informationContainer {
	display: grid;
	grid-template-columns: 0.5fr 1fr;
	grid-template-rows: 0.5fr;
	grid-template-areas: "image-Container textContainer";
	grid-area: informationContainer;
  }
  
  .image-Container { grid-area: image-Container; }
  
  .textContainer {
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 0.5fr 0.5fr;
	grid-template-areas: "dataContainer" "shareButton";
	grid-area: textContainer;
	padding-right: px;
  }
  
  .dataContainer{
	  padding-top: 25px;
	  grid-area: dataContainer;
  }

  .shareButton{
	grid-area: shareButton;
	padding-top: 5px;
}


  .shortLocationText{
	  font-size: 35px;
  }
  .truckIcon{
	  width: 65px;
	  height: 15px;
	  margin: 0 auto;
	  padding-top: 5px;
  }
  .fullLocationText{
	  font-size: 7px;
	  margin: 5px;
	  opacity: 0.5;
  }
  .forestImage{
	  width: 150px;
	  height: 150px;
	  padding: 10px 0px 10px 0px;
  }

  .dataContainer div{
	  padding: 2px 0px 2px 0px;
  }

  .dataContainer h2{
	  padding-top: 3px;
	color: #62BB9D;
  }
  .primaryInfo h1{
	  font-size: 12px;
	  font-weight: 500;
  }

  .primaryInfo h2{
	  font-size: 23px;
	  font-weight: 900;
  }

  .secondaryInfo h1 {
	  font-size: 7px;
	  font-weight: 500;
  }
  .secondaryInfo h2 {
	font-size: 10px;
	font-weight: 900;
}

.shareButton button{
	text-align: center;
	vertical-align: middle;
	width: 80px;
	height: 25px;
	border-radius: 5px;
	background-color: #62BB9D;
	border: none;
	color: #ffffff;
	opacity: 0.8;
	font-size: 8px;
	font-family: Montserrat,sans-serif;
	font-weight: bold;
}
  
/*  CarbonFootPrintData */
.carbonFootPrintContainer {
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 0.2fr 1.8fr;
	grid-template-areas: "title" "dataContainer";
  }
  
  .title { grid-area: title; 
}
  
