/*settings*/
.settingsContainer{
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 20px 20px;
	grid-template-areas: "notificationIcon settingsIcon resourcesIcon" "notificationText settingsText resourcesText";
	text-align: center;
	height: 25;
	margin-top: 15px;
	border-radius: 11px;
	box-shadow: 1px 1px 1px 1px;
  }
  
  .notificationIcon { grid-area: notificationIcon; }
  
  .settingsIcon { grid-area: settingsIcon; }
  
  .resourcesIcon { grid-area: resourcesIcon; }
  
  .notificationText { grid-area: notificationText; }
  
  .settingsText { grid-area: settingsText; }
  
  .resourcesText { grid-area: resourcesText; }

.settingsContainer img{
	padding: 5px;
	width:15px;

}


.settingsContainer p{
	padding: 5px;
	font-size: 8px;
}
